.chat {
    flex: 0.65;
    display: flex;
    flex-direction: column;
}

.chat__header{
    padding: 15px;
    display: flex;
    align-items: center;
    border-botton: 2px solid lightgray;
}

.chat__headerInfo {
    flex: 1;
    padding-left: 20px;
}

.chat__headerInfo > h3 {
    margin-bottom: 3px;
    font-weight: 500px;
}

.chat__headerInfo > p {
    color: grey;
}

.chat__headerRight {
    display: flex;
    justify-content: space-between;
    min-width: 100px;
}

.chat__body {
    flex: 1;    
    background-image: url("https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png");
    background-repeat: repeat;
    background-position: center;
    padding: 30px;
    overflow-y: scroll;
}

.chat__message {
    position: relative;
    font-size: 15px;
    padding: 10px;
    border-radius: 10px;
    width: fit-content;
    background-color: white;
    margin-bottom: 30px;
}

.chat__name {
    position: absolute;
    display: block;
    color: black;
    font-weight: bold;
    font-size: small;
    top: -15px;
}

.chat__timestamp {
    margin-left: 15px;
    position: relative;
    font-size: x-small;
    bottom: -5px;
}

.chat__receiver {
    margin-left: auto;
    background-color: #dcf8c6;
}

.chat__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60px;
    border-top: 2px solid lightgrey;
}

.chat__footer > form {
    flex: 1;
    display: flex;
}

.chat__footer > form > input {
    flex: 1;
    border-radius: 20px;
    font-size: 16px;
    outline: none;
    padding: 10px;
}

.chat__footer > form > button {
    display: none;
}

.chat__footer > .MuiSvgIcon-root {
    padding: 10px;
    color: grey;
    font-size: 2rem;
}